<template>
  <div class="container">
    <div class="wrapper">
      <div class="wrapper-title">
        <h1>My To Do List</h1>
      </div>
      <div class="wrapper-top">
        <input class="input-add" placeholder="Title..." type="text"
               v-model="myInput"
        />
        <button class="btn-add"
                @click="addItem"
        >Add</button>
      </div>
      <div class="wrapper-bottom">
        <ul>
          <li @click="e => e.target.classList.toggle('active')"
              :class="{ active: isActive}"
              class="item"
              v-bind:key="item"
              v-for="(item, index) in newItems">
            {{ index + 1 }} {{ item }}
            <button class="close"
            @click="closeItem(index, item)"
            >
              {{ close }}
            </button>
          </li>
        </ul>
        <div class="wrapper-quest">
          <div>Всего заданий: {{ newItems.length }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      myInput: '',
      newItems: [],
      close: '',
      isActive: false,
      number: '',
    }
  },

  methods: {
    addItem() {
      if (this.myInput !== '') {
        this.newItems.push(this.myInput);
        this.myInput = '';
        this.close = 'x'
      }
    },
    closeItem(index) {
      this.newItems.splice(index, 1);
    },
  },
}
</script>

<style scoped>
.item.active{
  text-decoration: line-through;
  background-color: grey;
}
</style>